import React from "react";
import Producto from "./Producto";
// import { FiShoppingCart } from "react-icons/bs";
import { ImCart } from "react-icons/im";
import { Fragment } from "react";

const Car = ({ car, setCar }) => {
  return (
    <div className="car">
      <div className="iconCar">
        <ImCart className="icon" />

        <h3 className="number"> {car.length} </h3>
        <div></div>
        <h3>Carrito</h3>
      </div>
      <div className="shop">
        {car.length === 0 ? (
          <p></p>
        ) : car.length === 0 ? (
          <p>0</p>
        ) : (
          car.map((producto) => (
            <Fragment className="shop">
              <Producto
                key={producto.id}
                producto={producto}
                car={car}
                setCar={setCar}
              ></Producto>
            </Fragment>
          ))
        )}
      </div>
    </div>
  );
};

export default Car;
